import React, { Component } from "react";
import axios from "axios";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  UncontrolledTooltip,
  Spinner,
} from "reactstrap";
import $ from "jquery";
import Timezone from "./timezone";

class userSettings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      passwordError: "",
      passwordPassed: "",
      passwordConfError: "",
      passwordConfPassed: "",
      passwordBorderColor: "#3F4E4F",
      passwordConfBorderColor: "#3F4E4F",
      passwordChangeResponse: "",
      logoutButtonState: false,
      requestErrorLogout: "",
    };
  }
  getCookie = (name) => {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].toString().replace(/^([\s]*)|([\s]*)$/g, "");
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  };
  logoutRequest = (event) => {
    event.preventDefault();
    this.spinnerSetter();
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "POST",
      url: "/api/auth/logout/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
    })
      .then((response) => {
        this.props.toggle();
        this.props.stateSwapper();
        this.props.onSuccess("Logout Successful!");
        this.spinnerSetter();
      })
      .catch((error) => {
        const errorToClean = error.response.request.responseText;
        const cleaned = errorToClean.split('"')[3];
        this.setState({ requestErrorLogout: cleaned });
        this.spinnerSetter();
      });
  };
  requestErrorLoginSetter = () => {
    return (
      <div className="text-center" style={{ color: "red", fontSize: "12" }}>
        {this.state.requestErrorLogout}
      </div>
    );
  };
  timezoneChangeRequest = (event) => {
    event.preventDefault();
  };
  spinnerSetter = () => {
    this.setState({ logoutButtonState: !this.state.logoutButtonState });
  };
  passwordChangeRequest = (event) => {
    event.preventDefault();
    const newUserPassword = $("#passwordField").val();
    const newUserPasswordConf = $("#passwordConfField").val();
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "post",
      url: "api/auth/password/change/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
      data: {
        new_password1: newUserPassword,
        new_password2: newUserPasswordConf,
      },
    })
      .then(() => {
        this.setState({
          passwordChangeResponse: "Password Changed Successfully!",
        });
      })
      .catch((error) => {
        this.setState({
          passwordChangeResponse:
            "An error has occured. Exit this popup and try again",
        });
      });
  };
  checkPasswordInput = () => {
    var currentInput = $("#passwordField").val();
    var passwordConf = $("#passwordConfField").val();
    if (currentInput.length < 8) {
      this.setState({ passwordPassed: "" });
      this.setState({ passwordError: "Password too short" });
      this.setState({ passwordBorderColor: "red" });
    } else if (passwordConf.length >= currentInput.length) {
      if (currentInput !== passwordConf) {
        this.setState({ passwordConfError: "Passwords must match" });
        this.setState({ passwordConfPassed: "" });
        this.setState({ passwordConfBorderColor: "red" });
        this.setState({ passwordError: "Passwords must match" });
        this.setState({ passwordPassed: "" });
        this.setState({ passwordBorderColor: "red" });
      } else {
        this.setState({ passwordConfError: "" });
        this.setState({ passwordError: "" });
        this.setState({ passwordPassed: "Looks good!" });
        this.setState({ passwordConfPassed: "Looks good!" });
        this.setState({ passwordBorderColor: "green" });
        this.setState({ passwordConfBorderColor: "green" });
      }
    } else {
      this.setState({ passwordError: "" });
      this.setState({ passwordPassed: "Looks good!" });
      this.setState({ passwordBorderColor: "green" });
    }
  };
  checkPasswordConfInput = () => {
    this.checkPasswordInput();
    var currentInput = $("#passwordConfField").val();
    var passwordToMatch = $("#passwordField").val();
    if (currentInput.length < passwordToMatch.length) {
      this.setState({ passwordConfPassed: "" });
      this.setState({ passwordConfError: "" });
      this.setState({ passwordConfBorderColor: "#3F4E4F" });
    } else if (currentInput !== passwordToMatch) {
      this.setState({ passwordPassed: "" });
      this.setState({ passwordError: "Passwords must match" });
      this.setState({ passwordBorderColor: "red" });
      this.setState({ passwordConfPassed: "" });
      this.setState({ passwordConfError: "Passwords must match" });
      this.setState({ passwordConfBorderColor: "red" });
    } else if (currentInput.length > 8 && currentInput === passwordToMatch) {
      this.setState({ passwordError: "" });
      this.setState({ passwordPassed: "Looks good!" });
      this.setState({ passwordBorderColor: "green" });
      this.setState({ passwordConfError: "" });
      this.setState({ passwordConfPassed: "Looks good!" });
      this.setState({ passwordConfBorderColor: "green" });
    }
  };
  paswordChangeButtonSetter = () => {
    if (
      this.state.passwordPassed === "Looks good!" &&
      this.state.passwordConfPassed === "Looks good!"
    ) {
      return (
        <Button className="add-button-reminder" type="submit">
          Set Change
        </Button>
      );
    } else {
      return (
        <div id="disabledButton" style={{ width: "fit-content" }}>
          <Button className="add-button-reminder" disabled>
            Set Change
          </Button>
          <UncontrolledTooltip placement="top" target="disabledButton">
            Fill out form to submit!
          </UncontrolledTooltip>
        </div>
      );
    }
  };
  logoutButtonState = () => {
    if (this.state.logoutButtonState === false) {
      return (
        <Button className="edit-button" type="submit">
          Logout
        </Button>
      );
    } else {
      return (
        <Button className="edit-button-reminder" disabled>
          Loading
          <Spinner size="sm">Loading...</Spinner>
        </Button>
      );
    }
  };
  passwordVisibilitySwitch = () => {
    if (document.getElementById("passwordField").type === "password") {
      $("#passwordField").attr("type", "text");
      $("#password-reveal").attr("class", "fa fa-eye-slash fa-lg eye-overlay");
    } else {
      $("#passwordField").attr("type", "password");
      $("#password-reveal").attr("class", "fa fa-eye fa-lg eye-overlay");
    }
  };
  passwordConfVisibilitySwitch = () => {
    if (document.getElementById("passwordConfField").type === "password") {
      $("#passwordConfField").attr("type", "text");
      $("#password-reveal-confirmation").attr(
        "class",
        "fa fa-eye-slash fa-lg eye-overlay"
      );
    } else {
      $("#passwordConfField").attr("type", "password");
      $("#password-reveal-confirmation").attr(
        "class",
        "fa fa-eye fa-lg eye-overlay"
      );
    }
  };
  render() {
    const { toggle } = this.props;
    return (
      <Modal id="settingsModal" isOpen={true} toggle={toggle}>
        <ModalHeader
          toggle={toggle}
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          Settings
        </ModalHeader>
        <Form onSubmit={this.logoutRequest}>
          <div
            style={{ float: "right", paddingRight: "1em", textAlign: "right" }}
          >
            <Label>
              Logged in As:
              <br /> {this.props.thisUser}
            </Label>
            <br />
            {this.logoutButtonState()}
            {this.requestErrorLoginSetter()}
          </div>
        </Form>
        <ModalBody style={{ backgroundColor: "#DCD7C9" }}>
          <Form onSubmit={this.timezoneChangeRequest}>
            <FormGroup>
              <Label>Change your timezone: </Label>
              <br />
              <Timezone setTimezone={this.setTimezone} />
              <div className="timezone-passed" id="timezone-tip">
                {this.state.timezoneConf}
              </div>
            </FormGroup>
            <Button
              className="add-button"
              // onClick={}
            >
              Set change
            </Button>
          </Form>
          <Label style={{ fontSize: "20px" }}>Password Change</Label>
          <br />
          <Form onSubmit={this.passwordChangeRequest}>
            <FormGroup style={{ borderRightColor: "#DCD7C9" }}>
              <Label>New Password:</Label>
              <Input
                type="password"
                id="passwordField"
                name="passwordField"
                placeholder="Minimum of 8 characters"
                onKeyUp={this.checkPasswordInput}
                minLength={8}
                required
                style={{ borderColor: this.state.passwordBorderColor }}
              />
              <button
                id="password-reveal"
                className="fa fa-eye fa-lg eye-overlay"
                type="button"
                onClick={this.passwordVisibilitySwitch}
              ></button>
            </FormGroup>
            <div className="input-error">{this.state.passwordError}</div>
            <div className="input-passed">{this.state.passwordPassed}</div>
            <FormGroup style={{ borderRightColor: "#DCD7C9" }}>
              <Label>Repeat New Password:</Label>
              <Input
                type="password"
                id="passwordConfField"
                name="passwordField"
                placeholder="Minimum of 8 characters"
                onKeyUp={this.checkPasswordConfInput}
                minLength={8}
                required
                style={{ borderColor: this.state.passwordConfBorderColor }}
              />
              <button
                id="password-reveal-confirmation"
                className="fa fa-eye fa-lg eye-overlay"
                type="button"
                onClick={this.passwordConfVisibilitySwitch}
              ></button>
            </FormGroup>
            <div className="input-error">{this.state.passwordConfError}</div>
            <div className="input-passed">{this.state.passwordConfPassed}</div>
            {this.paswordChangeButtonSetter()}
            {this.state.passwordChangeResponse}
          </Form>
        </ModalBody>
        <ModalFooter
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        ></ModalFooter>
      </Modal>
    );
  }
}

export default userSettings;
