import React, { Component } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  InputGroup,
  InputGroupText,
} from "reactstrap";
import Datetime from "react-datetime";
import "react-datetime/css/react-datetime.css";
import moment from "moment";

class CustomModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeReminder: this.props.activeReminder,
      dateSelect: this.props.date,
      timeSelct: this.props.time,
    };
  }

  disablePastDate = (current) => {
    const yesterday = moment().subtract(1, "day");
    return current.isAfter(yesterday);
  };

  handleChange = (e) => {
    let { name, value } = e.target;

    if (e.target.type === "checkbox") {
      value = e.target.checked;
    }

    const activeReminder = { ...this.state.activeReminder, [name]: value };

    this.setState({ activeReminder });
  };
  inputPlaceholderSetter = () => {
    if (`${this.state.dateSelect}` === "") {
      return {
        id: "datetimeModule",
        placeholder: "12/31/2022 12:00 AM",
      };
    } else {
      return {
        id: "datetimeModule",
        value: this.state.dateSelect + " " + this.state.timeSelct,
      };
    }
  };
  render() {
    const { toggle, onSave } = this.props;
    const inputProps = this.inputPlaceholderSetter();
    return (
      <Modal isOpen={true} toggle={toggle}>
        <ModalHeader
          toggle={toggle}
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          Reminder
        </ModalHeader>
        <ModalBody style={{ backgroundColor: "#DCD7C9" }}>
          <Form>
            <FormGroup>
              <Label for="reminder-title">Title</Label>
              <Input
                style={{ backgroundColor: "#DCD7C9", borderColor: "#3F4E4F" }}
                type="text"
                id="reminder-title"
                name="title"
                value={this.state.activeReminder.title}
                onChange={this.handleChange}
                placeholder="Enter Reminder Title"
              />
            </FormGroup>
            <FormGroup>
              <Label for="reminder-description">Description</Label>
              <Input
                style={{ backgroundColor: "#DCD7C9", borderColor: "#3F4E4F" }}
                type="text"
                id="reminder-description"
                name="description"
                value={this.state.activeReminder.description}
                onChange={this.handleChange}
                placeholder="Enter Reminder Description"
              />
            </FormGroup>
            <Label for="reminder-Time-Select">Select Reminder Time</Label>
            <InputGroup>
              <InputGroupText>
                <span className="fa fa-calendar"></span>
              </InputGroupText>
              <Datetime
                inputProps={inputProps}
                isValidDate={this.disablePastDate}
              />
            </InputGroup>
          </Form>
        </ModalBody>
        <ModalFooter
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          <Button
            className="add-button-reminder"
            onClick={() => onSave(this.state.activeReminder)}
          >
            Save
          </Button>
        </ModalFooter>
      </Modal>
    );
  }
}
export default CustomModal;
