import React, { Component } from "react";
import axios from "axios";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  UncontrolledTooltip,
  Spinner,
} from "reactstrap";
import $ from "jquery";
import Timezone from "./timezone";

class registrationModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showlogin: true,
      emailError: "",
      emailPassed: "",
      passwordError: "",
      passwordPassed: "",
      passwordConfError: "",
      passwordConfPassed: "",
      emailBorderColor: "#3F4E4F",
      passwordBorderColor: "#3F4E4F",
      passwordConfBorderColor: "#3F4E4F",
      passwordConfHolder: "",
      timezoneSet: false,
      timezoneConf: "This is used for reminder scheduling.",
      requestError: "",
      registerSpinner: false,
    };
  }
  registrationRequest = (event) => {
    event.preventDefault();
    this.spinnerSetter();
    const userEmail = $("#emailField").val();
    const userPassword = $("#passwordField").val();
    const userPasswordConf = $("#passwordConfField").val();
    const timezone_Shift = $("#timezone-select").val();
    axios({
      method: "post",
      url: "/api/auth/registration/",
      data: {
        username: userEmail,
        email: userEmail,
        password1: userPassword,
        password2: userPasswordConf,
        timezone_Shift: timezone_Shift,
      },
    })
      .then(() => {
        this.props.onSuccess("Registration Successful!");
        this.props.toggle();
        this.spinnerSetter();
      })
      .catch((error) => {
        const errorToClean = error.response.request.responseText;
        const cleaned = errorToClean.split('"')[3];
        this.setState({ requestError: cleaned });
        this.spinnerSetter();
      });
  };
  registationButtonSetter = () => {
    if (this.state.registerSpinner) {
      return (
        <Button className="add-button-reminder" disabled>
          Loading
          <Spinner size="sm">Loading...</Spinner>
        </Button>
      );
    } else if (
      this.state.emailPassed === "Looks good!" &&
      this.state.passwordPassed === "Looks good!" &&
      this.state.passwordConfPassed === "Looks good!" &&
      this.state.timezoneSet
    ) {
      return (
        <Button className="add-button-reminder" type="submit">
          Sign Up!
        </Button>
      );
    } else {
      return (
        <div id="disabledButton" style={{ width: "fit-content" }}>
          <Button className="add-button-reminder" disabled>
            Sign Up!
          </Button>
          <UncontrolledTooltip placement="top" target="disabledButton">
            Fill out form to enable!
          </UncontrolledTooltip>
        </div>
      );
    }
  };
  spinnerSetter = () => {
    this.setState({ registerSpinner: !this.state.registerSpinner });
  };
  passwordConfSetter = () => {
    if (this.state.passwordConfHolder === "") {
      return (
        <Input
          type="password"
          id="passwordConfField"
          name="passwordConfField"
          placeholder="Re-enter password"
          onKeyUp={this.checkPasswordConfInput}
          minLength={8}
          required
          style={{ borderColor: this.state.passwordConfBorderColor }}
        />
      );
    } else {
      return (
        <Input
          type="password"
          id="passwordConfField"
          name="passwordConfField"
          placeholder="Re-enter password"
          value={this.state.passwordConfHolder}
          onKeyUp={this.checkPasswordConfInput}
          minLength={8}
          required
          style={{ borderColor: this.state.passwordConfBorderColor }}
        />
      );
    }
  };
  passwordVisibilitySwitch = () => {
    if (document.getElementById("passwordField").type === "password") {
      $("#passwordField").attr("type", "text");
      $("#password-reveal").attr("class", "fa fa-eye-slash fa-lg eye-overlay");
    } else {
      $("#passwordField").attr("type", "password");
      $("#password-reveal").attr("class", "fa fa-eye fa-lg eye-overlay");
    }
  };
  passwordConfVisibilitySwitch = () => {
    if (document.getElementById("passwordConfField").type === "password") {
      $("#passwordConfField").attr("type", "text");
      $("#password-reveal-confirmation").attr(
        "class",
        "fa fa-eye-slash fa-lg eye-overlay"
      );
    } else {
      $("#passwordConfField").attr("type", "password");
      $("#password-reveal-confirmation").attr(
        "class",
        "fa fa-eye fa-lg eye-overlay"
      );
    }
  };
  timezoneSetter = () => {
    this.setState({ timezoneSet: true });
  };
  checkEmailInput = () => {
    var currentInput = $("#emailField").val();
    var re = /\S+@\S+\.\S+/;
    if (currentInput.length <= 3) {
      this.setState({ emailPassed: "" });
      this.setState({ emailError: "Email too short" });
      this.setState({ emailBorderColor: "red" });
    } else if (!re.test(currentInput)) {
      this.setState({ emailPassed: "" });
      this.setState({ emailError: "Please enter a valid email" });
      this.setState({ emailBorderColor: "red" });
    } else {
      this.setState({ emailError: "" });
      this.setState({ emailPassed: "Looks good!" });
      this.setState({ emailBorderColor: "green" });
    }
  };
  checkPasswordInput = () => {
    this.checkEmailInput();
    var currentInput = $("#passwordField").val();
    var passwordConf = $("#passwordConfField").val();
    if (currentInput.length < 8) {
      this.setState({ passwordPassed: "" });
      this.setState({ passwordError: "Password too short" });
      this.setState({ passwordBorderColor: "red" });
    } else if (passwordConf.length >= currentInput.length) {
      if (currentInput !== passwordConf) {
        this.setState({ passwordConfError: "Passwords must match" });
        this.setState({ passwordConfPassed: "" });
        this.setState({ passwordConfBorderColor: "red" });
        this.setState({ passwordError: "Passwords must match" });
        this.setState({ passwordPassed: "" });
        this.setState({ passwordBorderColor: "red" });
      } else {
        this.setState({ passwordConfError: "" });
        this.setState({ passwordError: "" });
        this.setState({ passwordPassed: "Looks good!" });
        this.setState({ passwordConfPassed: "Looks good!" });
        this.setState({ passwordBorderColor: "green" });
        this.setState({ passwordConfBorderColor: "green" });
      }
    } else {
      this.setState({ passwordError: "" });
      this.setState({ passwordPassed: "Looks good!" });
      this.setState({ passwordBorderColor: "green" });
    }
  };
  checkPasswordConfInput = () => {
    this.checkPasswordInput();
    var currentInput = $("#passwordConfField").val();
    var passwordToMatch = $("#passwordField").val();
    if (currentInput.length < passwordToMatch.length) {
      this.setState({ passwordConfPassed: "" });
      this.setState({ passwordConfError: "" });
      this.setState({ passwordConfBorderColor: "#3F4E4F" });
    } else if (currentInput !== passwordToMatch) {
      this.setState({ passwordPassed: "" });
      this.setState({ passwordError: "Passwords must match" });
      this.setState({ passwordBorderColor: "red" });
      this.setState({ passwordConfPassed: "" });
      this.setState({ passwordConfError: "Passwords must match" });
      this.setState({ passwordConfBorderColor: "red" });
    } else if (currentInput.length > 8 && currentInput === passwordToMatch) {
      this.setState({ passwordError: "" });
      this.setState({ passwordPassed: "Looks good!" });
      this.setState({ passwordBorderColor: "green" });
      this.setState({ passwordConfError: "" });
      this.setState({ passwordConfPassed: "Looks good!" });
      this.setState({ passwordConfBorderColor: "green" });
    }
  };
  requestErrorSetter = () => {
    return (
      <div className="text-center" style={{ color: "red", fontSize: "12" }}>
        {this.state.requestError}
      </div>
    );
  };
  render() {
    const { toggle } = this.props;
    return (
      <Modal
        id="registerModal"
        className="quick-buzz"
        isOpen={true}
        toggle={toggle}
      >
        <ModalHeader
          toggle={toggle}
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          User Registration
        </ModalHeader>
        <ModalBody style={{ backgroundColor: "#DCD7C9" }}>
          <Form onSubmit={this.registrationRequest}>
            <FormGroup>
              <Label>Email</Label>
              <Input
                style={{
                  backgroundColor: "#DCD7C9",
                  borderColor: this.state.emailBorderColor,
                }}
                type="email"
                id="emailField"
                name="emailField"
                placeholder="name@example.com"
                onKeyUp={this.checkEmailInput}
                required
              />
              <div className="input-error">{this.state.emailError}</div>
              <div className="input-passed">{this.state.emailPassed}</div>
            </FormGroup>
            <FormGroup>
              <Label>Password</Label>
              <Input
                type="password"
                id="passwordField"
                name="passwordField"
                placeholder="Minimum of 8 characters"
                onKeyUp={this.checkPasswordInput}
                minLength={8}
                required
                style={{ borderColor: this.state.passwordBorderColor }}
              />
              <button
                className="fa fa-eye fa-lg eye-overlay"
                id="password-reveal"
                type="button"
                onClick={this.passwordVisibilitySwitch}
              ></button>
            </FormGroup>
            <div className="input-error">{this.state.passwordError}</div>
            <div className="input-passed">{this.state.passwordPassed}</div>
            <FormGroup>
              <Label>Password Confirm</Label>
              {this.passwordConfSetter()}
              <button
                className="fa fa-eye fa-lg eye-overlay"
                id="password-reveal-confirmation"
                type="button"
                onClick={this.passwordConfVisibilitySwitch}
              ></button>
            </FormGroup>
            <div className="input-error">{this.state.passwordConfError}</div>
            <div className="input-passed">{this.state.passwordConfPassed}</div>
            <FormGroup>
              <Label>Pick your timezone: </Label>
              <br />
              <Timezone setTimezone={this.timezoneSetter} />
              <div className="timezone-passed" id="timezone-tip">
                {this.state.timezoneConf}
              </div>
            </FormGroup>
            {this.registationButtonSetter()}
            {this.requestErrorSetter()}
          </Form>
          <div className="signup-suggest">
            Already have an account?{" "}
            <button
              className="createAccoutButton"
              onClick={this.props.logInToggle}
            >
              Sign In.
            </button>
          </div>
        </ModalBody>
        <ModalFooter
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        ></ModalFooter>
      </Modal>
    );
  }
}

export default registrationModal;
