import React, { Component } from "react";
import axios from "axios";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  Spinner,
} from "reactstrap";
import $ from "jquery";

class AccountModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      spinnerSet: false,
      requestError: "",
    };
  }
  passwordVisibilitySwitch = () => {
    if (document.getElementById("passwordField").type === "password") {
      $("#passwordField").attr("type", "text");
      $("#password-reveal").attr("class", "fa fa-eye-slash fa-lg eye-overlay");
    } else {
      $("#passwordField").attr("type", "password");
      $("#password-reveal").attr("class", "fa fa-eye fa-lg eye-overlay");
    }
  };
  loginFlashSetter = () => {
    if (this.props.loginStatusFlash === "Login to continue") {
      return (
        <div>
          <div
            className="text-center"
            style={{
              fontSize: "24px",
              textDecoration: "underline",
              color: "#3F4E4F",
            }}
          >
            {this.props.loginStatusFlash}
          </div>
          <div
            className="text-center"
            style={{ fontSize: "14px", color: "#3F4E4F" }}
          >
            Otherwise take a look!
            <br /> However, you won't be able to save Tasks/Reminders
          </div>
        </div>
      );
    }
  };
  loginRequest = (event) => {
    this.spinnerSetter();
    event.preventDefault();
    const userEmail = $("#emailField").val();
    const userPassword = $("#passwordField").val();
    axios({
      method: "post",
      url: "/api/auth/login/",
      data: {
        username: userEmail,
        email: userEmail,
        password: userPassword,
      },
    })
      .then((response) => {
        this.props.toggle();
        this.props.onSuccess("Login Successful!");
        this.props.stateSwapper();
        this.spinnerSetter();
      })
      .catch((error) => {
        const errorToClean = error.response.request.responseText;
        const cleaned = errorToClean.split('"')[3];
        this.setState({ requestError: cleaned });
        this.spinnerSetter();
      });
  };
  requestErrorSetter = () => {
    return (
      <div className="text-center" style={{ color: "red", fontSize: "12" }}>
        {this.state.requestError}
      </div>
    );
  };
  loginButtonState = () => {
    if (this.state.spinnerSet === false) {
      return (
        <Button className="add-button" type="submit">
          Login
        </Button>
      );
    } else {
      return (
        <Button className="add-button" disabled>
          Loading
          <Spinner size="sm">Loading...</Spinner>
        </Button>
      );
    }
  };
  spinnerSetter = () => {
    this.setState({ spinnerSet: !this.state.spinnerSet });
  };
  render() {
    const { toggle } = this.props;
    return (
      //login Modal
      <Modal
        id="loginModal"
        isOpen={true}
        toggle={toggle}
        style={{ marginTop: "15vh" }}
      >
        <ModalHeader
          toggle={toggle}
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          User Login
        </ModalHeader>
        {this.loginFlashSetter()}
        <ModalBody style={{ backgroundColor: "#DCD7C9" }}>
          <Form onSubmit={this.loginRequest}>
            <FormGroup>
              <Label for="login-email">Email</Label>
              <Input
                style={{ backgroundColor: "#DCD7C9", borderColor: "#3F4E4F" }}
                type="text"
                id="emailField"
                name="emailField"
                placeholder="name@example.com"
                required
              />
            </FormGroup>
            <FormGroup>
              <Label for="login-password">Password</Label>
              <Input
                type="password"
                id="passwordField"
                name="passwordField"
                placeholder="Enter Your Password"
                required
              />
              <button
                className="fa fa-eye fa-lg eye-overlay"
                id="password-reveal"
                type="button"
                onClick={this.passwordVisibilitySwitch}
              ></button>
            </FormGroup>
            {this.loginButtonState()}
            {this.requestErrorSetter()}
          </Form>
          <div className="signup-suggest">
            Don't have an account?{" "}
            <button
              className="createAccoutButton"
              onClick={this.props.registrationToggle}
            >
              Create One.
            </button>
          </div>
        </ModalBody>
        <ModalFooter
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        ></ModalFooter>
      </Modal>
    );
  }
}

export default AccountModal;
