import React, { Component } from "react";
import TaskModal from "./components/Task-Modal";
import ReminderModal from "./components/Reminder-Modal";
import axios from "axios";
import styled, { keyframes } from "styled-components";
import LoginModal from "./components/LoginModal";
import { FormGroup, Input, Label, Button } from "reactstrap";
import { pulse, rotateIn } from "react-animations";
import UserSettings from "./components/userSettings";
import RegistrationModal from "./components/registrationModal";
import $ from "jquery";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentMode: "Tasks",
      spanSetting: "",
      viewCompleted: false,
      taskList: [],
      reminderList: [],
      loginModal: false,
      registerModal: false,
      modal: false,
      taskModal: true,
      userSettingsModal: false,
      userModals: false,
      userLoggedIn: false,
      loginStatusFlash: "",
      loginAddendum: "",
      userKey: "",
      successReporterText: "",
      userID: null,
      userEmail: "",
      activeTask: {
        title: "",
        description: "",
        completed: false,
      },
      activeReminder: {
        title: "",
        description: "",
        date: "",
        time: "",
        completed: false,
      },
    };
  }
  componentDidMount() {
    axios({
      method: "GET",
      url: "/api/auth/login/state/",
    }).then((response) => {
      if (response.data !== "false") {
        this.setState({ userLoggedIn: true });
        this.setState({ userID: response.data[1] });
        this.setState({ userEmail: response.data[0] });
        this.refreshTaskList();
        this.refreshReminderList();
      } else {
        this.setState({ loginStatusFlash: "Login to continue" });
        this.setState({
          loginAddendum: `Otherwise take a look! However, you won't be able to save Tasks/Reminders`,
        });
        this.setState({ loginModal: true });
      }
    });
  }
  logoutAddendum = () => {
    this.setState({
      taskList: [],
      reminderList: [],
      userID: null,
      userEmail: "",
    });
    this.successReporter("logout");
  };
  loginAddendum = () => {
    this.componentDidMount();
    this.successReporter("login");
  };
  getCookie = (name) => {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].toString().replace(/^([\s]*)|([\s]*)$/g, "");
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  };
  refreshTaskList = () => {
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "get",
      url: "/api/tasks/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
    })
      .then((response) => {
        this.setState({ taskList: response.data });
      })
      .catch((error) => {
        console.log(error.response.statusText);
      });
  };
  refreshReminderList = () => {
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "get",
      url: "/api/reminders/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
    })
      .then((response) => {
        this.setState({ reminderList: response.data });
      })
      .catch((error) => {
        console.log(error.response.statusText);
      });
  };

  toggle = () => {
    //task/reminder modal
    this.setState({ modal: !this.state.modal });
  };

  toggleLoginModal = () => {
    this.setState({ loginModal: !this.state.loginModal });
    this.setState({ registerModal: false });
    this.setState({ loginFlash: "" });
    this.setState({ loginAddendum: "" });
  };
  toggleRegisterModal = () => {
    this.setState({ registerModal: !this.state.registerModal });
    this.setState({ loginModal: false });
  };
  toggleUserSettingsModal = () => {
    this.setState({ userSettingsModal: !this.state.userSettingsModal });
    this.setState({ loginModal: false });
    this.setState({ registerModal: false });
  };

  handleTaskSubmit = (item) => {
    this.toggle();
    const cookie = this.getCookie("csrftoken");
    if (item.id) {
      axios({
        method: "put",
        url: `/api/tasks/${item.id}/`,
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-CSRFToken": cookie,
        },
        data: item,
      }).then((res) => this.refreshTaskList());
      return;
    }
    axios({
      method: "post",
      url: "/api/tasks/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
      data: item,
    }).then((res) => this.refreshTaskList());
  };
  handleReminderSubmit = (item) => {
    this.toggle();
    const datetime = document
      .getElementById("datetimeModule")
      .getAttribute("value")
      .split(" ");
    item.date = datetime[0];
    item.time = datetime[1] + " " + datetime[2];
    const cookie = this.getCookie("csrftoken");
    if (item.id) {
      axios({
        method: "put",
        url: `/api/reminders/${item.id}/`,
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-CSRFToken": cookie,
        },
        data: item,
      }).then((res) => this.refreshReminderList());
      return;
    }
    axios({
      method: "post",
      url: "/api/reminders/",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
      data: item,
    }).then((res) => this.refreshReminderList());
  };
  handleTaskDelete = (item) => {
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "delete",
      url: `/api/tasks/${item.id}/`,
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
      data: item,
    }).then((res) => this.refreshTaskList());
  };
  handleReminderDelete = (item) => {
    const cookie = this.getCookie("csrftoken");
    axios({
      method: "put",
      url: `/api/reminders/${item.id}/`,
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": cookie,
      },
      data: item,
    }).then((res) => this.refreshReminderList());
  };
  createTaskItem = () => {
    const item = {
      title: "",
      description: "",
      completed: false,
      author: this.state.userID,
    };

    this.setState({ activeTask: item, modal: !this.state.modal });
  };
  createReminderItem = () => {
    const item = {
      title: "",
      description: "",
      date: "",
      time: "",
      completed: false,
      author: this.state.userID,
    };

    this.setState({ activeReminder: item, modal: !this.state.modal });
  };

  editTaskItem = (item) => {
    this.setState({ activeTask: item, modal: !this.state.modal });
  };

  editReminderItem = (item) => {
    this.setState({ activeReminder: item, modal: !this.state.modal });
  };

  displayCompleted = (status) => {
    if (status) {
      return this.setState({ viewCompleted: true });
    }

    return this.setState({ viewCompleted: false });
  };
  deleteConfirmShow = (itemId) => {
    if (
      document.getElementById("true-delete" + itemId).className ===
      "show-true-delete"
    ) {
      $("#true-delete" + itemId).attr("class", "hidden-true-delete");
    } else {
      $("#true-delete" + itemId).attr("class", "show-true-delete");
    }
  };
  swapSetting = () => {
    if (this.state.currentMode === "Tasks") {
      this.setState({ currentMode: "Reminders" });
      this.setState({ spanSetting: "reminder" });
      this.setState({ reportColor: "#3F4E4F" });
      $("#index").attr("class", "body-reminder");
      $("#title").attr("class", "text-center my-4 reminder");
      $("#footer").attr("class", "copyright-reminder");
      $("#add_button").attr("class", "add-button-reminder hvr-shadow");
      $("#showing").attr("class", "showing-font-reminder");
      $("#userIcon").attr(
        "class",
        "user-reminder hvr-grow-rotate fa fa-user-circle-o fa-2x"
      );
    } else {
      this.setState({ currentMode: "Tasks" });
      this.setState({ spanSetting: "" });
      this.setState({ reportColor: "#A27B5C" });
      $("#index").attr("class", "body");
      $("#title").attr("class", "text-center my-4");
      $("#footer").attr("class", "copyright");
      $("#add_button").attr("class", "add-button hvr-shadow");
      $("#showing").attr("class", "showing-font");
      $("#userIcon").attr(
        "class",
        "user hvr-grow-rotate fa fa-user-circle-o fa-2x"
      );
    }
  };

  colorSet = () => {
    if (this.state.currentMode === "Tasks") {
      return "#3F4E4F";
    } else {
      return "#A27B5C";
    }
  };

  colorSetInverse = () => {
    if (this.state.currentMode === "Reminders") {
      return "#3F4E4F";
    } else {
      return "#A27B5C";
    }
  };
  loginStateSwapper = () => {
    this.setState({ userLoggedIn: !this.state.userLoggedIn });
  };
  renderTabList = () => {
    return (
      <div className="nav nav-tabs">
        <span
          onClick={() => this.displayCompleted(true)}
          className={
            this.state.viewCompleted
              ? "nav-link active-" + this.state.spanSetting
              : "nav-link-" + this.state.spanSetting
          }
        >
          Complete
        </span>
        <span
          onClick={() => this.displayCompleted(false)}
          className={
            this.state.viewCompleted
              ? "nav-link-" + this.state.spanSetting
              : "nav-link active-" + this.state.spanSetting
          }
        >
          Incomplete
        </span>
      </div>
    );
  };
  addButtonRender = () => {
    if (this.state.currentMode === "Tasks") {
      return (
        <Button
          className="btn btn-primary add-button hvr-shadow"
          id="add_button"
          onClick={this.createTaskItem}
        >
          Add Task
        </Button>
      );
    } else {
      return (
        <Button
          className="btn btn-primary add-button hvr-shadow"
          id="add_button"
          onClick={this.createReminderItem}
        >
          Add Reminder
        </Button>
      );
    }
  };
  renderItems = () => {
    const RotateP = styled.p`
      animation: ${keyframes`${rotateIn}`} 1s;
    `;
    if (this.state.currentMode === "Tasks") {
      const { viewCompleted } = this.state;
      const newItems = this.state.taskList.filter(
        (item) => item.completed === viewCompleted
      );
      // Tasks mode
      return newItems.map((item) => (
        <li
          key={item.id}
          className="list-group-item d-flex justify-content-between align-items-center"
        >
          <span
            style={{ color: "#A27B5C" }}
            className={`task-title mr-2 ${
              this.state.viewCompleted ? "completed-task" : ""
            }`}
            title={item.description}
          >
            {item.title}
          </span>
          <span>
            <button
              className="edit-button hvr-shadow"
              onClick={() => this.editTaskItem(item)}
            >
              Edit
            </button>
            <button
              className="delete-button hvr-shadow"
              onClick={() => this.deleteConfirmShow(`${item.id}`)}
            >
              Delete
            </button>
            <button
              id={`true-delete${item.id}`}
              className="hidden-true-delete"
              onClick={() => this.handleTaskDelete(item)}
            >
              <RotateP>
                <i className="fa fa-minus-square" aria-hidden="true" />
              </RotateP>
            </button>
          </span>
        </li>
      ));
      // Reminder Mode
    } else {
      const { viewCompleted } = this.state;
      const newItems = this.state.reminderList.filter(
        (item) => item.completed === viewCompleted
      );
      return newItems.map((item) => (
        <li
          key={item.id}
          className="list-group-item d-flex justify-content-between align-items-center"
        >
          <span
            style={{ color: "#3F4E4F" }}
            className={`mr-2 ${
              this.state.viewCompleted ? "completed-task" : ""
            }`}
            title={item.description}
          >
            {item.title}
          </span>
          <span>
            <button
              className="edit-button-reminder hvr-shadow"
              onClick={() => this.editReminderItem(item)}
            >
              Edit
            </button>
            <button
              className="delete-button-reminder hvr-shadow"
              onClick={() => this.deleteConfirmShow(`${item.id}`)}
            >
              Delete
            </button>
            <button
              id={`true-delete${item.id}`}
              className="hidden-true-delete"
              onClick={() => this.handleReminderDelete(item)}
            >
              <RotateP>
                <i className="fa fa-minus-square" aria-hidden="true" />
              </RotateP>
            </button>
          </span>
        </li>
      ));
    }
  };

  modalSetter = () => {
    if (this.state.currentMode === "Tasks") {
      return this.state.modal ? (
        <TaskModal
          activeTask={this.state.activeTask}
          toggle={this.toggle}
          onSave={this.handleTaskSubmit}
        />
      ) : null;
    } else {
      return this.state.modal ? (
        <ReminderModal
          activeReminder={this.state.activeReminder}
          toggle={this.toggle}
          onSave={this.handleReminderSubmit}
          date={this.state.activeReminder.date}
          time={this.state.activeReminder.time}
        />
      ) : null;
    }
  };
  successReporter = (report) => {
    if (report === "logout") {
      this.setState({ successReporterText: "Logout Successful!" });
    } else if (report === "register") {
      this.setState({ successReporterText: "Registration Successful!" });
    } else {
      this.setState({ successReporterText: "Login Successful!" });
    }
    if (this.state.currentMode === "Tasks") {
      this.setState({ reportColor: "#A27B5C" });
    } else {
      this.setState({ reportColor: "#3F4E4F" });
    }
    setTimeout(() => {
      $("#successReport").addClass("fadeOut");
    }, 3000);
    setTimeout(() => {
      this.setState({ successReporterText: "" });
      $("#successReport").removeClass("fadeOut");
    }, 5000);
  };
  userModalSetter = () => {
    if (this.state.userLoggedIn && this.state.userSettingsModal) {
      return (
        <UserSettings
          toggle={this.toggleUserSettingsModal}
          stateSwapper={this.loginStateSwapper}
          onSuccess={this.successReporter && this.logoutAddendum}
          thisUser={this.state.userEmail}
        />
      );
    } else if (this.state.loginModal) {
      return (
        <LoginModal
          toggle={this.toggleLoginModal}
          loginStatusFlash={this.state.loginStatusFlash}
          loginAddendum={this.state.loginAddendum}
          stateSwapper={this.loginStateSwapper}
          registrationToggle={this.toggleRegisterModal}
          onSuccess={this.successReporter && this.loginAddendum}
        />
      );
    } else if (this.state.registerModal) {
      return (
        <RegistrationModal
          toggle={this.toggleRegisterModal}
          logInToggle={this.toggleLoginModal}
          onSuccess={this.successReporter && this.loginAddendum}
        />
      );
    }
  };
  userIconClick = () => {
    if (this.state.userLoggedIn) {
      this.toggleUserSettingsModal();
    } else {
      this.toggleLoginModal();
    }
  };
  render() {
    const PulseDiv = styled.div`
      animation: ${keyframes`${pulse}`} 2s;
    `;
    return (
      <div className="index" id="page">
        <div className="cd-intro-content">
          <button
            className="user hvr-grow-rotate fa fa-user-circle-o fa-2x user-hidder"
            id="userIcon"
            onClick={this.userIconClick}
          ></button>
          <main className="container">
            <h1
              className="text-center my-4"
              id="title"
              style={{ color: this.colorSetInverse() }}
            >
              Note Keeper
            </h1>
            <div className="row">
              <div className="col-lg-6 col-md-8 col-sm-10 mx-auto">
                <PulseDiv>
                  <div
                    className="text-center"
                    id="successReport"
                    style={{ color: this.state.reportColor }}
                  >
                    {this.state.successReporterText}
                  </div>
                </PulseDiv>
                <div className="card p-5">
                  <FormGroup switch>
                    <Input
                      id="mainSwitch"
                      type="switch"
                      role="switch"
                      onClick={this.swapSetting}
                    />
                    <Label className="showing-font" id="showing" check>
                      Showing: {this.state.currentMode}
                    </Label>
                  </FormGroup>
                  <div className="mb-2">{this.addButtonRender()}</div>
                  {this.renderTabList()}
                  <ul className="list-group list-group-flush border-top-0">
                    {this.renderItems()}
                  </ul>
                </div>
              </div>
            </div>
            {this.modalSetter()}
            {this.userModalSetter()}
          </main>
        </div>
      </div>
    );
  }
}
export default App;
