import React, { Component } from "react";

import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
} from "reactstrap";

class TaskModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTask: this.props.activeTask,
    };
  }

  handleChange = (e) => {
    let { name, value } = e.target;

    if (e.target.type === "checkbox") {
      value = e.target.checked;
    }

    const activeTask = { ...this.state.activeTask, [name]: value };

    this.setState({ activeTask });
  };

  render() {
    const { toggle, onSave } = this.props;
    return (
      <Modal isOpen={true} toggle={toggle}>
        <ModalHeader
          toggle={toggle}
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          Task
        </ModalHeader>
        <ModalBody style={{ backgroundColor: "#DCD7C9" }}>
          <Form>
            <FormGroup>
              <Label for="task-title">Title</Label>
              <Input
                style={{ backgroundColor: "#DCD7C9", borderColor: "#3F4E4F" }}
                type="text"
                id="task-title"
                name="title"
                value={this.state.activeTask.title}
                onChange={this.handleChange}
                placeholder="Enter Task Title"
              />
            </FormGroup>
            <FormGroup>
              <Label for="task-description">Description</Label>
              <Input
                style={{ backgroundColor: "#DCD7C9", borderColor: "#3F4E4F" }}
                type="text"
                id="task-description"
                name="description"
                value={this.state.activeTask.description}
                onChange={this.handleChange}
                placeholder="Enter Task Description"
              />
            </FormGroup>
            <FormGroup check>
              <Label check>
                <Input
                  id="taskcheck"
                  type="checkbox"
                  name="completed"
                  checked={this.state.activeTask.completed}
                  onChange={this.handleChange}
                />
                Completed
              </Label>
            </FormGroup>
          </Form>
        </ModalBody>
        <ModalFooter
          style={{ backgroundColor: "#DCD7C9", borderColor: "#DCD7C9" }}
        >
          <Button
            className="add-button"
            onClick={() => onSave(this.state.activeTask)}
          >
            Save
          </Button>
        </ModalFooter>
      </Modal>
    );
  }
}
export default TaskModal;
